.hero-wrapper {
    color: white;
    position: relative;
    padding-bottom: 2rem;
    background-image: url('./maderaa.jpg'); /* Añade la ruta de tu imagen */
    background-size: cover; /* Asegura que la imagen cubra todo el contenedor */
    background-position: center; /* Centra la imagen */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    min-height: 90vh; /* Asegúrate de que cubra al menos la altura de la ventana */
  }
  
  .hero-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem; /* Ajusta el padding según sea necesario */
  }
  
  .hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  .hero-right {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image-container {
    width: 100%;
    max-width: 600px; /* Ajusta el tamaño máximo de la imagen */
    height: auto;
    overflow: hidden;
    border-radius: 1rem; /* Ajusta según la curvatura deseada */
    margin-top: 70px; 
  }
  
  .image-container > img {
    border-radius: 1rem;
    width: 100%;
    height: auto;
  }
  


  .hero-title {
    position: relative;
    z-index: 1;
    padding-top: 8rem; /* Ajusta según sea necesario */
    padding-bottom: 1rem; /* Ajusta según sea necesario */
  }
  
  .hero-title > h1 {
    font-weight: 800;
    font-size: 6rem; /* Ajusta según sea necesario */
    line-height: 1;
    margin: 0;
  }
  
  .orange-circle {
    height: 4rem;
    width: 4rem;
  }
  
  .hero-des {
    font-size: 1.2rem; /* Ajusta según sea necesario */
    line-height: 1.5;
  }
  
  @media (max-width: 768px) {
    .hero-container {
      flex-direction: column;
      text-align: center;
    }
  
    .hero-left, .hero-right {
      flex: none;
      width: 100%;
    }
  
    .hero-title > h1 {
      font-size: 2.5rem; /* Ajusta para pantallas más pequeñas */
    }
  
    .image-container {
      max-width: 100%;
    }
  }
  