/* Estilos para el contenedor del menú */
.menu {
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  background-color: #000000; /* Fondo negro */
  padding: 20px;
  border-radius: 10px; /* Esquinas redondeadas */
}

/* Estilos para los elementos del menú */
.menu-item {
  cursor: pointer;
  text-align: center;
  transition: transform 0.2s;
  color: white; /* Texto blanco */
}

/* Efecto hover para los elementos del menú */
.menu-item:hover {
  transform: scale(1.1);
}

/* Efecto activo para los elementos del menú */
.menu-item:active {
  transform: scale(0.9);
}

/* Estilos para las imágenes de los elementos del menú */
.menu-item img {
  width: 130px;
  height: 100px;
  border-radius: 20px; /* Esquinas redondeadas */
  transition: box-shadow 0.2s;
}

/* Efecto hover para las imágenes */
.menu-item img:hover {
  box-shadow: 0 4px 8px rgb(37, 37, 37);
}

/* Estilos para el texto debajo de las imágenes */
.menu-item span {
  display: block;
  margin-top: 10px;
  color: #d3d2d2; /* Color gris claro */
}

/* Estilos para el botón de contacto */
.contact-button {
  background-color: blue; /* Fondo azul */
  color: white; /* Texto blanco */
  border: none;
  padding: 10px 20px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s;
  border-radius: 5px; /* Esquinas redondeadas */
}

.contact-button:hover {
  background-color: darkblue; /* Fondo azul oscuro al hacer hover */
}
